import { useEffect, useRef, useState } from "react";
import Layout from "../common/Layout";

export default function Location(){

    const { kakao } = window;

    const info = [
        {
            title:" ",
            latlng: new kakao.maps.LatLng(37.5116828, 127.059151),
            imgUrl: `${process.env.PUBLIC_URL}img/marker1.png`,
            imgSize: new kakao.maps.Size(232,99),
            imgPos: {offset: new kakao.maps.Point(116, 69)},
        },
        {
            title:" ",
            latlng: new kakao.maps.LatLng(37.4941355, 126.7681066),
            imgUrl: `${process.env.PUBLIC_URL}img/marker2.png`,
            imgSize: new kakao.maps.Size(232,99),
            imgPos: {offset: new kakao.maps.Point(116, 69)},
        },
        {
            title:" ",
            latlng: new kakao.maps.LatLng(37.5258975, 126.9284261),
            imgUrl: `${process.env.PUBLIC_URL}img/marker3.png`,
            imgSize: new kakao.maps.Size(232,99),
            imgPos: {offset: new kakao.maps.Point(116, 69)},
        }
    ];


    const container = useRef(null);
    const btns = useRef(null);

    const [Location, setLocation] = useState(null); //useEffct     state 
    const [Traffic, setTraffic] = useState(false); // Traffic     state ,  .    
    const [Info] = useState(info); //setInfo  info     
    const [Index, setIndex] = useState(0); //    useState  


    // 
    const option = {
        center: Info[Index].latlng,  // 0 Index 
        level: 3
    }

    //
    const markerPosition = Info[Index].latlng;
    const imageSrc = Info[Index].imgUrl;
    const imageSize = Info[Index].imgSize;
    const imageOption = Info[Index].imgPos ;

    const markerImage = new kakao.maps.MarkerImage(
        imageSrc, imageSize, imageOption
    );

    const marker = new kakao.maps.Marker({
        position : markerPosition,
        image : markerImage
    });

    //    
    useEffect(()=>{
        const map_instance = new kakao.maps.Map(container.current, option);
        marker.setMap(map_instance);
        setLocation(map_instance);

        const mapTypeControl = new kakao.maps.MapTypeControl();
       map_instance.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

       const zoomControl = new kakao.maps.ZoomControl();
        map_instance.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

        // for (const btn of btns.current.children) btn.classList.remove("on");
        // btns.current.children[Index].classList.add("on");

        window.addEventListener("resize", ()=>{
            map_instance.setCenter(Info[Index].latlng);
        });
    },[Index]); //<---         , Index        

    //  useEffect
    useEffect(()=>{
        if(!Location) return;
         //Location state                

         //      , 
         Traffic
         ? Location.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
         : Location.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC);
    },[Traffic]);  //<---[Traffic] traffic state      

    return(
        <Layout name={"Location"}>
            <div id="map" ref={container}></div>
            
            <div className="btnSet">
            {/*   ,   =>   true false false true  */}
            <button onClick={()=>{setTraffic(!Traffic)}}>
                
            {/*       */}
            {Traffic ? 'Traffic Off' : 'Traffic On'}
            </button>

            <ul className="branch" /*ref={btns}*/>
                { Info.map((el, idx)=>{
                    let on='';
                    Index === idx ? (on='on') : (on = '');
                    return(
                        <li key={idx} onClick={()=> setIndex(idx)} className={on}>{el.title}</li>
                    );})}
            </ul>


            </div>
            
            

        </Layout>

    );
    }

/* 
    
<Layout name={"Location"}>
            <div id="map" ref={container}></div>

            <button onClick={()=>{
                Location.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
            }}>
                Traffic On
            </button>
            <button onClick={()=>{
                Location.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
            }}>
                Traffic off
            </button>

        </Layout>
         */

/*  3
<ul className="branch">
                //    Index   
                <li onClick={() => setIndex(0)}> </li>
                <li onClick={() => setIndex(1)}> </li>
                <li onClick={() => setIndex(2)}> </li>
            </ul>
*/
