import Layout from "../common/Layout";
import Popup from "../common/Popup";
import axios from "axios";
import { useState, useEffect, useRef } from "react";
import Masonry from 'react-masonry-component'



export default function Gallery() {
    
    /*
    masonryOptions ;  ?
    https://www.npmjs.com/package/react-masonry-component  install 
      ,  import  .
*/

const masonryOptions = { transitionDuration: '0.5s' };
const [Items, setItems] = useState([]);    
const [Loading, setLoading] = useState(true);
const [EnableClick, setEnableClick] = useState(true);
const [Index, setIndex] = useState(0);
const frame = useRef(null);
const input = useRef(null);
const pop = useRef(null);  
 //useRef  pop .    forwardRef 

/*
interest  
getFlickr({
    type: 'interest',
})
search  
getFlickr({
    type:'search',
    tags: '',
})
*/

const getFlickr = async (opt) => {
    const key = '4612601b324a2fe5a1f5f7402bf8d87a';
    const method_interest = "flickr.interestingness.getList";
    const method_search = "flickr.photos.search";
    const method_user = "flickr.people.getPhotos";
    const num = 50; //50 
    let url = '';


    if (opt.type === 'interest') {
        url = `https://www.flickr.com/services/rest/?method=${method_interest}&per_page=${num}&api_key=${key}&format=json&nojsoncallback=1`;
    }
    if (opt.type === 'search') {
        url = `https://www.flickr.com/services/rest/?method=${method_search}&per_page=${num}&api_key=${key}&format=json&nojsoncallback=1&tags=${opt.tags}`;
    }
    if (opt.type === 'user') {
        url = `https://www.flickr.com/services/rest/?method=${method_user}&per_page=${num}&api_key=${key}&format=json&nojsoncallback=1&user_id=${opt.user}`;
    }
    
    
     // await axios.get(url).then((json) => {
        //     setItems(json.data.photos.photo);
        // })

    // //async  , (url)
    // const getFlickr = async (url) => {
    //     const result = await axios.get(url);
    //     setItems(result.data.photos.photo);

    const result = await axios.get(url);
    if(result.data.photos.photo.length === 0) return alert('   .');
    // console.log(result.data.photos.photo);
    setItems(result.data.photos.photo);
        

    //   1     false  on   .

        setTimeout(()=>{    
           setLoading(false);
            frame.current.classList.add('on'); 
            setTimeout(()=>{
                setEnableClick(true);  //     
            },500); // on   0.5s 
            
        },1000); //   masonry  1
        
    };

    //   
    // const getFlickr = async (url) => {
    //     await axios.get(url).then((json)=>{
    //         setItems(json.data.photos.photo);
    //     })
    // };

    
    //      
    
    // useEffect(()=>{
    //     axios.get(url).then((json)=>{         //json  .
    //         console.log(json.data.photos.photo);
    //     })
    // },[]);

    useEffect(() => getFlickr({ type: 'search', tags: "sky" }), []);
    //()=>getFlickr '()=>'  ,    ,       .
    const showSearch = () => {
        const result = input.current.value.trim();
        input.current.value = '';
        if(!result) return alert(' .');
        if (!EnableClick) return;
        setEnableClick(false);
        setLoading(true);
        frame.current.classList.remove('on');
        getFlickr({ type: 'search' });
    };

    // ->

 


    //https://live.staticflickr.com/{server-id}/{id}_{secret}_{size-suffix}.jpg
    return (
        <>
            <Layout name={'Gallery'}>
                {Loading && (
                    <img
                        className="loading"
                        src={`${process.env.PUBLIC_URL}/img/6.gif`}
                    />
                )}

                <div className="controls">
                    <nav>
                        <button
                            onClick={() => {
                                if (!EnableClick) return;
                                // false return 
                                setEnableClick(false);
                                //true   false   
                                setLoading(true);
                                frame.current.classList.remove('on');
                                getFlickr({ type: 'interest' });
                            }}
                        >
                            Interest Gallery
                        </button>
                        <button
                            onClick={() => {
                                if (!EnableClick) return;
                                setEnableClick(false);
                                setLoading(true);
                                frame.current.classList.remove('on');
                                getFlickr({ type: 'user', user: "196629878@N05" });
                            }}
                        >
                            My Gallery
                        </button>
                    </nav>
                    <div className="searchBox">
                    <input type="text" ref={input} placeholder=' '
                            onKeyUp={(e) => {
                                if (e.key === 'Enter') showSearch();
                            }} />
                        <button
                            onClick={showSearch}
                        >Search</button>
                    </div>
                </div>
                

                <div className="frame" ref={frame}>
                    <Masonry elementType={'div'} options={masonryOptions}>


                    {Items.map((item,idx)=>{
                        return(
                            <article key={idx}>
                                <div className="inner">
                                    <div className="pic" 
                                    onClick={()=>{
                                        pop.current.open(); //popup 
                                        setIndex(idx); // youtube 
                                    }}
                                    >
                                        <img 
                                        src={`https://live.staticflickr.com/${item.server}/${item.id}_${item.secret}_m.jpg`} 
                                        alt={item.title} />
                                    </div>
                                    <h2>{item.title}</h2>
                                    
                                    <div className="profile">
                                        <img src={`http://farm${item.farm}.staticflickr.com/${item.server}/buddyicons/${item.owner}.jpg`} alt={`${item.owner}`} 
                                        onError={(e)=>{e.target.setAttribute('src','https://www.flickr.com/images/buddyicon.gif');
                                        }}
                                        />
                                        <span
                                            onClick={(e)=>{
                                                if (!EnableClick) return;
                                                setEnableClick(false);
                                                setLoading(true);
                                                frame.current.classList.remove('on');
                                                getFlickr({ type: 'user', user: e.target.innerText });   
                                            }}
                                        >{item.owner}</span>
                                    </div>
                                </div>
                            </article>
                        )
                    })}
                    </Masonry>                
                </div>
            </Layout>

            <Popup ref={pop}>
                {Items.length !== 0 && (  
// {Items.length !== 0 && (    
                    <img 
                     src={`https://live.staticflickr.com/${Items[Index].server}/${Items[Index].id}_${Items[Index].secret}_b.jpg`} alt={Items[Index].title} />
                )}
            </Popup>
        </>
        
    );
}

