function Pics({ Scrolled, start}){
    // =  || ;
    //    undefined,Nan  Scrolled - start  0   0 
    const position = Scrolled - start || 0;
    //position =>        ,
    //   0.

   // console.log(Scrolled);

    return(
        <main id='pics' className='myScroll'>
           <p 
            style={{left : 100 + position,}}
        // style={
        //     position >=0
        //     ? { left :100 + position, }
        //     : null }
        >FLICKR</p>
        <h3
            style ={{
                left : 100 + position / 2,
            }}
        >FLICKR</h3>
        </main>
    );
}

export default Pics;