import { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import Layout from '../common/Layout';

function Signup() {
    const history = useHistory();
    /*
    useHistory  : url    hook.
      url       . 
     url        
    url          ,
             url   
    "  "  .
    
    useHistory    . 
      5 useHistory   6 useNavigate .
    */

    //   state   
    const initVal = {
        userid: '',
        email: '',
        pwd1: '',
        pwd2: '',
        comments:'',
        edu: '',
        gender: null,
        interests: null ,
    };

    //  initVal  state   
    const [Val, setVal] = useState(initVal);
    const [Err, setErr] = useState({});
    const [Submit, setSubmit] = useState(false);

    const check = (value) => {
        const errs = {};

        const eng = /[a-zA-Z]/;
        const num = /[0-9]/;
        const spc = /[~!@#$%^&*()_+]/;



        if (value.userid.length < 5) {
            errs.userid = ' 5  ';  }
        // 8  @ 
        if (value.email.length < 8 || !/@/.test(Val.email) ){
            errs.email = ' 8 , @ ';   }
        if(value.pwd1 <5 || 
            !eng.test(value.pwd1) ||
            !num.test(value.pwd1) ||
            !spc.test(value.pwd1)
            )
            {errs.pwd1 = ' 5 , , ,    ';}
            //  ,    
        if(value.pwd1 !== value.pwd2 || value.pwd2 < 5 ){
            errs.pwd2 = "   .";
        }
        //gender ?
        if(!Val.gender){
            errs.gender = " .";
        }

        if(!Val.interests){
            errs.interests = '.';
        }
        
        if(Val.comments.length < 20){
            errs.comments = '  20  ';
        }

        if(Val.edu === ''){
            errs.edu = ' ';
        }

        return errs;
    };

    const handleChange = (e) => {
        // 2 -    ,    
        const { name, value } = e.target;
        // 3 -     Val state  
        // 4 -setVal      
        setVal({ ...Val, [name]: value });
    }

    const handleRadio=(e)=>{
        const {name} = e.target;
        const isChecked= e.target.checked;
        setVal({...Val, [name]: isChecked});
    }

    const handleSelect=(e)=>{
        const {name} = e.target;
        const isSelected = e.target.value;
        setVal({...Val, [name]:isSelected});
    }

    const handleCheck=(e)=>{
        let isChecked = false;
        const {name} = e.target;
        const inputs = e.target.parentElement.querySelectorAll('input');
        //  .
        inputs.forEach((el)=>{
            if(el.checked) isChecked = true;
            });
            setVal({...Val, [name]: isChecked})
    }

    const handleReset = (e)=>{
        setSubmit(false);
        setErr({});
        setVal(initVal);
    }    
    
    const handleSubmit = (e) => {
        // 6   
        e.preventDefault();
        // 7 Val state   check   <={ check(Val)}
        setErr(check(Val));
        // 8   errs     Err state  
    };

    useEffect(()=>{
        const len = Object.keys(Err).length;
        if(len === 0 && Submit){
            alert(' .  .');
            history.push('/youtube')
        }
    },[Err]);


    return (
        <Layout name={'Member'}>
            {/*  5     */}
            <form onSubmit={handleSubmit}>
                <fieldset>
                    <legend className='h'>  </legend>
                    <table border='1'>
                        <caption className='h'> </caption>
                        <tbody>
                            {/* userid */}
                            <tr>
                                <th scope='row'>
                                    <label htmlFor="userid">USER ID</label>
                                </th>
                                <td>
                                    <input type="text"
                                        placeholder=' '
                                        name='userid'
                                        id='userid'

                                        value={Val.userid}
                                        //1 -      
                                        onChange= { handleChange }
                                    />
                                    {/*  9      */}
                                    <span className='err'>{Err.userid}</span>
                                </td>
                            </tr>

                            {/* password */}
                            <tr>
                                <th scope='row'>
                                    <label htmlFor="pwd1">PASSWORD</label>
                                </th>
                                <td>
                                    <input type="password" name="pwd1" id="pwd1" placeholder=' ' onChange={handleChange} />
                                    <span className='err'>{Err.pwd1}</span>
                                </td>
                            </tr>
                            <tr>
                                <th scope='row'>
                                    <label htmlFor="pwd2">RE-PASSWORD</label>
                                </th>
                                <td>
                                    <input type="password" name="pwd2" id="pwd2" placeholder=' ' onChange={handleChange} />
                                    <span className='err'>{Err.pwd2}</span>
                                </td>
                            </tr>

                            {/* email */}
                            <tr>
                                <th scope='row'>
                                    <label htmlFor="email">E-MAIL</label>
                                </th>
                                <td>
                                    <input type="text"
                                            id='email'
                                            name='email'
                                            placeholder='  .'
                                            value={Val.email}
                                            onChange={handleChange}
                                    />
                                    <span className='err'>{Err.email}</span>
                                </td>
                            </tr>
                            {/* edu */}
                            <tr>
                                <th scope='row'>
                                    <label htmlFor="edu">EDUCATION</label>
                                </th>
                                <td>
                                    <select name="edu" id="edu" onChange={handleSelect}>
                                        <option value=""> </option>
                                        <option value="elementary"></option>
                                        <option value="middle"></option>
                                        <option value="high"></option>
                                        <option value="college"></option>
                                    </select>
                                    <span className='err'>{Err.due}</span>
                                </td>
                            </tr>

                            {/* gender */}
                            <tr>
                                <th scope='row'>GENDER</th>
                                <td>
                                    <label htmlFor="male">MALE</label>
                                    <input type="radio" name="gender" id="male" onChange={handleRadio}/>
                                    
                                    <label htmlFor="female">FEMALE</label>
                                    <input type="radio" name="gender" id="female" onChange={handleRadio} />
                                    
                                    <span className='err'>{Err.gender}</span>
                                </td>
                            </tr>

                            {/* check box */}
                            <tr>
                                <th scope='row'>INTEREST</th>
                                <td>
                                    <label htmlFor="sports">SPORTS</label>
                                    <input type="checkbox" name="interests" id="sports" onChange={handleCheck}/>

                                    <label htmlFor="music">MUSIC</label>
                                    <input type="checkbox" name="interests" id="music" onChange={handleCheck}/>

                                    <label htmlFor="game">GAME</label>
                                    <input type="checkbox" name="interests" id="game" onChange={handleCheck}/>
                                    <span className='err'>{Err.interests}</span>
                                </td>
                            </tr>

                            {/* comments */}
                            <tr>
                                <th scope='row'>
                                    <label htmlFor="comments">COMMENSTS</label>
                                </th>
                                <td>
                                    <textarea name="comments" id="comments" cols="30" rows="5" value={Val.comments} onChange={handleChange} ></textarea>
                                    <span className='err'>{Err.comments}</span>    
                                </td>

                            </tr>
                


                            {/* btn set */}
                            <tr>
                                <th colSpan='2'>
                                    <input type="reset" value="CANCLE" onClick={handleReset}/>
                                    <input type="submit" value="SEND" onClick={()=>setSubmit(true)}/>
                                </th>
                            </tr>
                        </tbody>
                    </table>
                </fieldset>

            </form>
        </Layout>

    );
}
export default Signup;
